

$spacer: 1rem;

$spacers: (
        0: 0,
        1: $spacer * 0.25,
        2: $spacer * 0.5,
        3: $spacer,
        4: $spacer * 1.5,
        5: ($spacer * 2),
        6: ($spacer * 3),
        7: ($spacer * 4),
        8: ($spacer * 6),
        9: ($spacer * 5.5),
        10: ($spacer * 0.75),
        11: ($spacer * 0.9375)
);

$grid-gutter-width:           30px;

$enable-negative-margins:     true;

$list-inline-padding:         2rem !default;

$border-width-big:            0.25rem;

.border-big         { border: $border-width-big solid; }
.border-top-big     { border-top: $border-width-big solid; }
.border-bottom-big  { border-bottom: $border-width-big solid; }
.border-start-big   { border-left: $border-width-big solid; }
.border-end-big     { border-right: $border-width-big solid; }

//old syntax (< bootstrap v5)
.border-left-big    { border-left: $border-width-big solid; }
.border-right-big   { border-right: $border-width-big solid; }

// Modal

$modal-content-border-width:  0;
$modal-lg:                    52.75rem;
$modal-md:                    34.875rem;
$modal-dialog-margin:         1rem;

// Alert
$alert-padding-y:             1rem;
$alert-padding-x:             1rem;

$alert-bg-level:              80%;

$grid-breakpoints: (
        xs: 0,
        sm: 576px,
        md: 768px,
        lg: 992px,
        xl: 1200px
);

$container-max-widths: (
        sm: 540px,
        md: 720px,
        lg: 960px,
        xl: 1140px
);


//as in bootstrap4 (bootstrap 5 is 1 rem)
$popover-body-padding-y:            .5rem !default;
$popover-body-padding-x:            .75rem !default;

