.flyout-login {
  z-index: 100;
  margin-top: remCalc(1);
  width: remCalc(400);
}

.headerLogin-button {
  cursor: pointer;

  &.active {
    border-bottom: remCalc(1) solid $primary;
  }
}

.flyout-close {
  cursor: pointer;
  position: absolute;
  top: $spacer * 0.5;
  right: $spacer * 0.5;
}

.userFormLogin-mobile {
  .btn-register,
  .btn-login {
    max-width: remCalc(280);
  }
}

.flyout-link {
  a {
    display: block;
    padding: 0.25rem 0;
  }
}

