.siteLogo-component {
  img {
    width: auto;
    max-width: 100%;
    height: remCalc(40);
    @include media-breakpoint-up(md) {
      height: remCalc(78);
    }
  }
}

.siteLogo-component-checkout,
.siteLogo-component-orderConfirmation {
  img {
    width: remCalc(120); // 120.204
    height: remCalc(32);
  }
}