$sideMenuWidth: calc(100vw - 32px);
$sideMenuWidthMdUp: map-get($grid-breakpoints, md) * 0.88;
$animationTime: .5s;

.canvas {
  transition: $animationTime;
  position: relative;
  left: 0;
  right: 0;

  &.moved-from-sidebar {
    width: 100%;
    &.left {
      left: $sideMenuWidth;
    }
    &.right {
      left: -#{$sideMenuWidth};
    }
  }
}

.sidebar {
  visibility: hidden;
  width: $sideMenuWidth;
  position: fixed;
  top: 0;
  bottom: 0;
  transition: $animationTime;

  &.sidebar-left {
    left: -100%;
    &.active {
      left: 0;
    }

    .sidebar-closer {
      right: remCalc(-36);
      top: 0;
    }
  }

  &.sidebar-right {
    //Negieren der SideMenuWith
    right: calc(-1 * #{$sideMenuWidth});
    &.active {
      right: 0;
    }

    .sidebar-closer {
      right: 0;
      top: 0;
    }
  }

  &.active {
    visibility: visible;
  }

  // Verhindert Fehler im Crome bei touch-events
  touch-action: manipulation;

  .sidebar-slot {
    // Ermöglicht es flex Containern kleiner als Parent zu werden
    min-height: 0;
  }

  .sidebar-upper {
    min-height: 0;
  }

  .hide-on-scroll {
    @supports (-webkit-overflow-scrolling: auto) {
      -webkit-overflow-scrolling: auto;
    }

    position: absolute;
    bottom: 0;
    transition: all ease-in-out $animationTime;
  }
}

.min-vh-105 {
  min-height: 105vh;
}

@include media-breakpoint-up(md) {
  .canvas {
    &.moved-from {
      &.left {
        left: $sideMenuWidthMdUp;
      }
      &.right {
        left: -$sideMenuWidthMdUp;
      }
    }
  }

  .sidebar {
    width: $sideMenuWidthMdUp;

    &.sidebar-left {
      left: -$sideMenuWidthMdUp;
    }

    &.sidebar-right {
      right: -$sideMenuWidthMdUp;
    }
  }
}

.sidebar-overlay {
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: opacity $animationTime;
  opacity: 0;

  &.active {
    visibility: visible;
    opacity: 0.3;
  }
}
