$sideMenuWidth: calc(100vw - 36px);
$animationTime: .5s;

.mobile-side-menu {

  .sideMenuOverlay {
    visibility: hidden;
    // Border bleibt sichtbar
    width: $sideMenuWidth;
    position: fixed;
    top: 0;
    bottom: 0;
    left: -100%;
    background-color: $white;
    transition: $animationTime;

    &.active {
      @include z-index(1);
      visibility: visible;
      left: 0;
    }

    .scrollable {
      overflow-y: auto;
    }

    .sideMenuOverlayHeadline {
      bottom: -1px;
    }
  }

  .btn-collapsible {
    &.collapse-sidemenu::after {
      background-image: getIconUrl("chevronUp", $dark, 24);
    }

    &.collapsed {
      &.collapse-sidemenu::after {
        background-image: getIconUrl("chevronDown", $dark, 24);
      }
    }
  }
}

.sideMenuOpener {
  width: remCalc(40);
}

//Exklusiver Actionbar Platzhalter für IOS Geräte
@supports (-webkit-overflow-scrolling: touch) {
  .header-icons-mobile {
    padding-bottom: remCalc(32);
  }
}


