.default-icon-favorites {
  @extend .default-icon-favoritesPin ;
}

.default-icon-favorites-checked {
  @extend .default-icon-favoritesPinFill;
}

.header-icon:hover .default-icon-favorites {
  @extend .default-icon-favoritesPinFill;
}

// Theme svg-icon buttons
// icon-width kommt aus theme-buttons
.btn {
  &.icon-left-favorites {
    @extend .icon-left-favoritesPin;
  }

  &.icon-left-favorites-checked::before {
    @include getIcon("favoritesPinFill", $default-icon-color, 24);
  }

  &.btn-sm.icon-left-favorites-checked::before {
    @include getIcon("favoritesPinFill", $default-icon-color, 20);
  }
}
