@include media-breakpoint-up(lg) {
  .container {
    padding-left: 0;
    padding-right: 0;
  }
}

@include media-breakpoint-down(lg) {
  .container {
    max-width: none;
  }
}

@include media-breakpoint-only(md) {
  .container {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.popover {
  z-index: 1;
}

.rounded, .rounded-circle {
  background-clip: padding-box;
}

.close {
  float: none;
  opacity: 1;
  // in bootstrap 5 statt close gibt es btn-close
  @extend .btn-close;
}

.alert-dismissible {
  .close {
    padding: 0.5rem;
  }
}

//alte klassen (< bootstrap v5)
.sr-only {
  @extend .visually-hidden;
}

.sr-only-focusable {
  @extend .visually-hidden-focusable;
}

//in bootstrap5 hat die farbe currentColor
hr {
  background-color: rgba($black, .1);
}

// no-gutters würde mit .g-0 ersetzt
.no-gutters {
  @extend .g-0;
}
