$icon-width: 24;
$icon-width-sm: 20;

@mixin buttonIconsForWidth($fill-color, $width) {
  $icon-map: getButtonIconUrlMap($fill-color, $width);
  @each $key, $bgImageUrl in $icon-map {
    &.icon-left-#{$key}::before {
      background-image: $bgImageUrl;
      width: #{$width}px;
      height: #{$width}px;
    }
  }
  @each $key, $bgImageUrl in $icon-map {
    &.icon-right-#{$key}::after {
      background-image: $bgImageUrl;
      width: #{$width}px;
      height: #{$width}px;
    }
  }
}

@mixin buttonIcons($fill-color) {
  @include buttonIconsForWidth($fill-color, $icon-width);
  &.btn-sm {
    @include buttonIconsForWidth($fill-color, $icon-width-sm);

    &::before {
      vertical-align: text-top;
    }
  }
}

// buttons

// inline buttons with min-width 160px
// overwrite this with .btn-block

.btn {
  max-width: 100%;

  &.disabled,
  &:disabled {
    cursor: default;
  }

  &.btn-stretch {
    padding-left: remCalc(15);
    padding-right: remCalc(15);
  }

  &:not(.btn-link) {
    text-decoration: none;
  }

  &:not(.btn-stretch):not(.btn-icon):not(.btn-icon-only){
    min-width: remCalc(160);
  }

  &[class^="icon-left-"], &[class*=" icon-left-"] {
    &::before {
      content: '';
      position: relative;
      display: inline-block;
      left: 0;
      top: 0;
      vertical-align: bottom;
      margin-right: remCalc(8);
    }
  }

  &[class^="icon-right-"], &[class*=" icon-right-"], &.btn-collapsible {
    &::after {
      content: '';
      position: relative;
      display: inline-block;
      right: 0;
      top: 0;
      vertical-align: bottom;
      margin-left: remCalc(8);
    }
  }

  &.btn-sm {
    font-weight: 400;

    &[class^="icon-right-"], &[class*=" icon-right-"],
    &[class^="icon-left-"], &[class*=" icon-left-"] {
      &::before {
        margin-right: 0;
      }
      &::after {
        margin-left: 0;
      }
    }
  }

  &.btn-right-icon {
    position: relative;
    padding-left: remCalc(15);
    padding-right: remCalc(47);
    text-align: left;

    &[class^="icon-right-"], &[class*=" icon-right-"] {
      &::after {
        position: absolute;
        margin-top: remCalc(7);
        margin-bottom: remCalc(7);
        margin-right: remCalc(15);
      }
    }
  }

  &.btn-collapsible {
    position: relative;
    padding-left: remCalc(15);
    padding-right: remCalc(47);
    text-align: left;

    &::after {
      position: absolute;
      width: #{$icon-width}px;
      height: #{$icon-width}px;
      margin-top: remCalc(7);
      margin-bottom: remCalc(7);
      margin-right: remCalc(15);
      background-image: getIconUrl("minus", $dark, $icon-width);
    }

    &.collapsed::after {
      background-image: getIconUrl("plus", $dark, $icon-width);
    }
  }

  @mixin btn-icon-only {
    position: relative;
    width: remCalc(40);
    height: remCalc(40);
    color: transparent;
    overflow: hidden;

    &[class^="icon-right-"], &[class*=" icon-right-"],
    &[class^="icon-left-"], &[class*=" icon-left-"] {
      &::before {
        margin: remCalc(7);
        position: absolute;
      }
      &::after {
        margin: remCalc(7);
        position: absolute;
      }
    }
  }

  &.btn-icon-only {
    @include btn-icon-only;
  }

  &.btn-icon {
    padding-left: remCalc(7);
    padding-right: remCalc(7);

    @include media-breakpoint-down(md) {
      @include btn-icon-only;
    }
  }
}

// textlinks (als Button, nicht inline-Text)
.btn-link {
  font-weight: 600;
  text-decoration: $link-decoration;
  @include buttonIcons($dark);

  &:hover {
    text-decoration: $link-hover-decoration;
  }

  &.disabled,
  &:disabled {
    text-decoration: none;
    color: $gray-secondary;
    @include buttonIcons($gray-secondary);
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    text-decoration: none;
  }
}

// color variants
.btn-dark {
  color: $white;
  @include buttonIcons($white);

  &:hover,
  &:focus {
    background-color: $primary;
    border-color: $primary;
  }

  &.disabled,
  &:disabled {
    background-color: $secondary;
    border-color: $secondary;
    color: $gray;
    @include buttonIcons($gray);
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    background-color: $dark;
    border-color: $dark;
    @include buttonIcons($white);
  }
}

.btn-dark-alt {
  color: $white;
  @include buttonIcons($white);

  &:hover,
  &:focus {
    background-color: transparent;
    border-color: $dark;
    color: $dark;
    @include buttonIcons($dark);
  }

  &.disabled,
  &:disabled {
    background-color: $secondary;
    border-color: $secondary;
    color: $gray;
    @include buttonIcons($gray);
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    background-color: transparent;
    border-color: $dark;
    color: $dark;
    @include buttonIcons($dark);
  }
}

.btn-outline-dark {
  @include buttonIcons($dark);

  &:hover,
  &:focus {
    background-color: $dark;
    border-color: $dark;
    color: $white;
    @include buttonIcons($white);
  }
}

.btn-primary {
  color: $white;
  @include buttonIcons($white);

  &:hover,
  &:focus {
    background-color: $dark;
    border-color: $dark;
    color: $white;
    @include buttonIcons($white);
  }

  &.disabled,
  &:disabled {
    color: $white;
    background-color: $primary;
    border-color: $primary;
    opacity: 50%;
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    color: $white;
    background-color: $dark-light;
    border-color: $dark-light;
    @include buttonIcons($white);
