    background-color: $gray-surface-light;
    border-color: $gray-surface-light;
    color: $secondary;
    @include buttonIcons($secondary);
  }

  &:not(:disabled):not(.disabled):active,
  &:not(:disabled):not(.disabled).active {
    background-color: $gray-dark;
