//der breakpoint wird berechnet als das minimum des nächsten abzüglich 0.02px
// breakpoints (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px) bsp: md ist 767.98px
// in Bootstrap 5 wird hier 991.98 geliefert
@mixin media-breakpoint-next-down($name, $breakpoints: $grid-breakpoints, $breakpoint-names: map-keys($breakpoints)) {
  $n: index($breakpoint-names, $name);
  $next: if($n != null and $n < length($breakpoint-names), nth($breakpoint-names, $n + 1), null);
  $max: if($next, breakpoint-min($next, $breakpoints) - .02, null);
  @if $max {
    @media (max-width: $max) {
      @content;
    }
  } @else {
    @content;
  }
}

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-next-down($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    @if ($infix != "") {
      .rounded#{$infix}-0 {
        border-radius: 0 !important;
      }
    }
  }
}

.border-color-red {
  border-color: $red;
}

.border-transparent {
  border-color: transparent !important;
}
