$arrowHeight: calc(50% - 54px);

.category-slider {
  .category-button {
    @extend .position-absolute;
    @extend .w-100;
    @extend .px-4;
    @extend .px-lg-5;
    bottom: 10%;

    .btn {
      @extend .btn-white-80;
    }
  }

  .category-button-sm {
    @extend .btn-gray-dark;
    @extend .px-1;
    @extend .py-4;
  }

  .slick-slide {
    &:not(.slick-current) {
      img {
        filter: grayscale(100%);
      }
    }
  }

  .category-slide {
    @extend .position-relative;
  }

  .category-slider-next {
    @extend .p-2;
    right: remCalc(-35);
    top: $arrowHeight;

    @include media-breakpoint-only(lg) {
      right: remCalc(-25);
    }

    .icon-category-slider-next {
      @extend .icon-chevronRight-white-92;
    }
  }

  .category-slider-prev {
    @extend .p-2;
    left: remCalc(-35);
    top: $arrowHeight;

    @include media-breakpoint-only(lg) {
      left: remCalc(-25);
    }

    .icon-category-slider-prev {
      @extend .icon-chevronLeft-white-92;
    }
  }
}
