.double-teaser-category-component {

  max-width: remCalc(1300);

  .outer-col-left {
    margin-right: remCalc(10);
  }

  .outer-col-right {
    margin-left: remCalc(10);
  }

  .bottom-10 {
    bottom: 10%;
  }

  .bottom-20 {
    bottom: 20%;
  }

  .image-container{
    @include media-breakpoint-up(md) {
      max-height: 55vh;
    }


    @include media-breakpoint-up(lg) {
      max-height: remCalc(600);
    }
  }
}
