.custom-checkbox {
  padding-left: remCalc(28);
  min-height: remCalc(24);

  .custom-control-input ~ .custom-control-label {
    @extend .display-23;

    &::before {
      border-color: $gray-primary;
      height: remCalc(16);
      width: remCalc(16);
      top: remCalc(4);
      left: remCalc(-28)
    }

    &::after {
      height: remCalc(16);
      width: remCalc(16);
      top: remCalc(4);
      left: remCalc(-28)
    }

    &--center {
      &::before,
      &::after {
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }

  .custom-control-input:not(:disabled) ~ .custom-control-label {
    cursor: pointer;
  }

  .custom-control-input:focus, .custom-control-input:hover {
    ~ .custom-control-label {
      text-decoration: underline;

      &::before {
        background-color: $gray-surface;
      }
    }
  }

  .custom-control-input:disabled ~ .custom-control-label {
    &::before {
      border-color: $gray-line;
      background-color: initial;
    }
  }

  .custom-control-input:checked ~ .custom-control-label {
    @extend .display-22;

    &::before {
      background-color: $primary;
      border-color: $primary;
    }

    &::after {
      @include getIcon("checkmarkSmall", $white, 45);
      background-position: 50% 50%;
      background-size: contain;
      height: remCalc(16);
      width: remCalc(16);
      top: remCalc(4);
    }
  }

  .custom-control-input:checked:disabled ~ .custom-control-label {
    &::before {
      opacity: 0.5;
      background-color: $primary;
      border-color: $primary;
    }
  }

  .custom-control-input:checked:focus, .custom-control-input:checked:hover {
    ~ .custom-control-label {
      &::before {
        background-color: desaturate(shade-color($primary, 8), 24);
        border-color: desaturate(shade-color($primary, 8), 24);
      }
    }
  }
}
