/* stylelint-disable-line property-no-unknown */
@use "sass:math";
$mobile-spacing: math.div($grid-gutter-width, 2);

.mobile-full-width {
  margin-right: (-$mobile-spacing);
  margin-left: (-$mobile-spacing);
  @include media-breakpoint-up(md) {
    margin-right: 0;
    margin-left: 0;
  }
}

//in bootstrap5 col Klassen hat kein position mehr.
@each $breakpoint in map-keys($grid-breakpoints) {
  $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

  @if $grid-columns > 0 {
    // Allow columns to stretch full width below their breakpoints
    @for $i from 1 through $grid-columns {
      .col#{$infix}-#{$i} {
        position: relative;
      }
    }
  }

  .col#{$infix},
  .col#{$infix}-auto {
    position: relative;
  }
}
