.seotext {
  column-count: 2;

  h2, .h2, h3, .h3, h4, .h4, a {
    @extend .text-secondary;
  }

  h2, .h2 {
    @extend .display-11;
  }

  h3, .h3 {
    @extend .display-7;
  }

  h4, .h4 {
    @extend .display-9;
  }
}

.readMoreButton {
  bottom: 0;
  right: 0;
  left: 0;
  height: 10rem;
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 1) 45%);
}

.seo-arrow-down {
  @include getIcon("chevronDown", $secondary, 24);
}

@include media-breakpoint-down(md) {
  .seotext {
    column-count: auto;
  }
  .seotext-mobile-collapsed {
    overflow: hidden;
    max-height: 460px;
  }
}
