$border-width: remCalc(2);

.product-markers {
  top: $border-width;
  min-height: remCalc(22);

  &.product-page-markers{
    padding-left: remCalc(8);

    @include media-breakpoint-up(md) {
      padding-left: remCalc(10);
    }

    @include media-breakpoint-up(lg) {
      padding-left: remCalc(92);
    }

    @include media-breakpoint-up(xl) {
      padding-left: remCalc(104);
    }
  }

  .product-marker {
    border-bottom: $border-width solid;

    &:last-child {
      // important benötigt um me-3 von BT zu überschreiben
      margin-right: 0 !important;
    }
  }
}
