.checkout-arrow {
  bottom: remCalc(-28);
  right: calc(50% - 28px);

}
.checkout-arrow::after {
  content: "";
  position:absolute;
  bottom: 0;
  left: 0;
  width: remCalc(56);
  height: remCalc(28);
  border-bottom: remCalc(1) solid $gray-line;
  border-left: remCalc(1) solid $gray-line;
  border-right: remCalc(1) solid $gray-line;
  border-bottom-left-radius: remCalc(100);
  border-bottom-right-radius: remCalc(100);
}