// feste Bildgröße
$gallery-image-width-lg: 60px;
$gallery-image-width-xl: 73px;

.product-gallery {

  .product-gallery-img {
    cursor: pointer;
  }

  @include media-breakpoint-only(lg) {
    width: $gallery-image-width-lg;
  }
  @include media-breakpoint-up(xl) {
    width: $gallery-image-width-xl;
  }

  .slick-slide img {
    min-height: remCalc(20);
  }
}

.product-carousel-prev {
  left: 0;
  top: calc(50% - 20px);
  opacity: 0.7;
}

.product-carousel-next {
  right: 0;
  top: calc(50% - 20px);
  opacity: 0.7;
}

.sumbox-carousel-prev {
  left: 0;
  top: calc(50% - 47px);
  opacity: 0.7;

  @include media-breakpoint-up(md) {
    left: remCalc(-9);
  }
}

.sumbox-carousel-next {
  right: 0;
  top: calc(50% - 47px);
  opacity: 0.7;

  @include media-breakpoint-up(md) {
    right: remCalc(-9);
  }
}

.product-image-container {
  max-width: remCalc(468);
}

.skeleton-image {
  width: 100%;
  padding-bottom: calc(100% / (2 / 3));

  > img {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}

.product-img-skeleton {
  .slick-dots {
    position: absolute;
    bottom: remCalc(12);
    display: block;
    width: 100%;
    max-width: calc(100vh - 32px);
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
  }

  .slick-dots li {
    position: relative;
    display: inline-block;
    margin: 0 remCalc(4);
    cursor: pointer;

    &:nth-child(11) {
      display: none;
    }
  }


  .slick-dots li button {
    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
    width: remCalc(24);
    padding: 0;
    margin: 0;

    &:before {
      @extend .shadow-lg;
      @extend .rounded;
      position: absolute;
      top: 0;
      left: 0;
      background-color: $white;
      content: '';
      width: remCalc(24);
      height: remCalc(4);
    }
  }

  .slick-dots li.slick-active button::before {
    height: remCalc(8);
    background-color: $red-coral;
  }

  .slick-dots li.slick-active {
    display: inline-block !important;
  }

  // max 10 dots , die Balkenbreite mit Abstand ist 32px.
  @media (max-width: 320px) {
    .slick-dots li {
      button {
        max-width: remCalc(20);

        &:before {
          max-width: remCalc(20);
        }
      }
    }
    @for $i from 1 through 10 {
      .slick-dots[data-dots-number="#{$i}"] {
        li:nth-child(n+#{$i}) {
          display: none;
        }
      }
    }
  }
}
