// Fix für scrollenden Hintergrund im Safari
// https://stackoverflow.com/questions/54786905/how-to-disable-body-scrolling-when-modal-is-open-ios-only/54798936#54798936
body {
  &.locked {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    overflow: hidden;
  }
}

// siehe auch _modal.scss von Bootstrap
.modal-dialog-centered .modal-content.modal-content-full-height {
  min-height: subtract(100vh, $modal-dialog-margin * 2);
}

@include media-breakpoint-up(sm) {
  .modal-dialog-centered .modal-content.modal-content-full-height {
    min-height: subtract(100vh, $modal-dialog-margin-y-sm-up * 2);
  }
}


// bootstrap 4. das brauchen wir in JS breite vom scroll-Balken zu rechnen
.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: remCalc(50);
  height: remCalc(50);
  overflow: scroll;
}

.modal-content {
  @extend .rounded;
}

.modal.modal-static .modal-dialog {
  transform: none;
}
