.form-control {
  height: auto;
}

.custom-select {
  background: none;
  @include getIcon("chevronDown", $gray-primary, 25);
  background-repeat: no-repeat;
  background-position: calc(100% - 0.7rem) 50%;
  width: 100%;
  height: auto;

  &.select-small {
    @extend .display-29;
    padding: 0.1875rem 0.9375rem;
  }

  &::-ms-expand {
    display: none;
  }
}

.custom-search {
  height: auto;

  //Form leeren "X" unter IE11 und Edge nicht anzeigen
  ::-ms-clear {
    display: none;
  }

  .search-button {
    &:hover {
      background-color: transparent !important;
    }

    height: remCalc(40);

    &::before {
      @include getIcon("search", $gray-primary, 25);
      content: '';
      position: relative;
      display: inline-block;
    }

    &:focus {
      outline: none;
      background-color: transparent !important;
    }
  }

  .search-box-input {
    @extend .rounded-0;
    @extend .rounded-start;

    + .invalid-feedback {
      order: 1;
    }
  }

  .search-box-input:focus ~ button {
    @extend .border-dark;
  }

  .is-invalid button.search-box-button {
    @extend .border-danger;
  }

  .has-warning button.search-box-button {
    @extend .border-warning;
  }

  .search-box-button {
    @include transition($input-transition);

    &[disabled]:before {
      display: none;
    }
  }

  .search-button-absolute {
    top: 0;
    right: 0;
  }

  .search-input {
    @include placeholderColor($gray-primary);
  }
}

.invalid-feedback {
  position: relative;

  &::before {
    @include getIcon("close", $white, 16);
    background-color: $red;
    background-position: 50% 50%;
    border-radius: 50%;
    content: "";
    left: 0;
    top: remCalc(4);
    position: absolute;
  }
}

.is-invalid {
  .form-control {
    border-color: $red;
  }

  .invalid-feedback {
    display: block;
  }
}

.has-warning:not(.is-invalid) .form-control {
  border-color: $warning;
  background-color: tint-color($warning, $alert-bg-level);
}

.has-warning-border:not(.is-invalid) .form-control {
  border-color: $warning;
}

.payment-radio-buttons {

  min-height: remCalc(72);

  &::before {
    top: 1.5rem;
  }

  &::after {
    top: 1.5rem;
  }
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form-checkbox-border {
  &.parsley-error {
    @extend .border-danger;
  }
  &:not(.parsley-error) {
    @extend .border-transparent;
  }
}
