// Neue Icons zu $button-icon-list aus theme-svg-icon-variables.scss hinzufügen
$button-icon-list: join($button-icon-list, (
        chevronRight,
        chevronLeft,
        chevronDown,
        plus,
        minus,
        pencil,
        close,
        pin,
));

// $default-icon-color und $default-icon-size kommen aus theme-svg-icon-variables.scss
$default-icon-url-map: getIconUrlMap($default-icon-color, $default-icon-size);

@each $icon-name, $icon-url in $default-icon-url-map {
  .default-icon-#{$icon-name} {
    @include getIcon($icon-name, $default-icon-color, $default-icon-size)
  }
}

