@use "sass:math";
$navigation-list-spacing: math.div($grid-gutter-width, 2);

.first-level-nav, .second-level-nav{
  a:hover {
    text-decoration: none;
  }
}

.first-level-nav {
  .nav-elem{
    &:hover {
      @extend .border-dark;
    }
  }
}

.second-level-nav {
  .nav-elem{
    &.active, &:hover {
      @extend .border-primary;
    }
  }

  nav {
    display: none;

    &.active {
      display: block;
    }
  }

  .navigation-flyout-opener {
    cursor: pointer;
  }
}

.second-level-nav-mobile {
  overflow-y: auto;

  .collapse-sidemenu.active {
    background-color: $light;
  }
}

.navigation-flyout {
  height: remCalc(548);

  .navigation-flyout-closer {
    top: 0;
    right: 0;
  }

  .navigation-list {
    margin-right: (-$navigation-list-spacing);
    margin-left: (-$navigation-list-spacing);

    .navigation-headline {
      height: 0;
      position: relative;
      top: -3rem;
    }

    .navigation-element {
      padding-right: $navigation-list-spacing;
      padding-left: $navigation-list-spacing;
    }

    .navigation-linebreak {
      width: 0;
      height: 101%;
    }
  }
}
