.sale-component {
  max-width: remCalc(1600);

  .headline {
    width: 100vw;
    max-width: remCalc(475);
    border-top: remCalc(5) $black solid;
  }

  .sale-component-text {
    height: 33.33%;
    @include media-breakpoint-up(lg) {
      height: remCalc(295);
    }
  }

  .sale-image {
    height: remCalc(295);
    @include media-breakpoint-up(lg) {
      height: remCalc(295);
    }
  }
}

