@use "sass:math";
.list-custom-checkmark {
  list-style-type: none;
  @extend .display-23;
  > * {
    @extend .position-relative;
    &::before {
      display: inline-block;
      position: absolute;
      @extend .icon-checkmark-green-20;
      left: $spacer * -1.5;
      top: math.div($spacer, 16);
      content: '';
    }
  }
  &.display-21 {
    > * {
      &::before {
        top: math.div($spacer, 5.333);
      }
    }
  }
}

.list {
  &--dashed {
    list-style: none;

    > li:before {
      display: inline-block;
      content: "-";
    }
  }
}
