$modal-outer-spacing: 32px;
$modal-outer-spacing-mobile: 16px;
$modal-inner-spacing-mobile: 16px;
$arrow-size: 40px;
$slide-item-spacing: 16px;
$slide-width: 60px;
$slickslide-outerwidth: $slide-width + 48px; // padding-left + padding-right

.openseadragon-container {
  @extend .rounded;
  z-index: 0;
}

.product-image-viewer-modal {
  height: calc(100vh - #{$modal-outer-spacing-mobile});
  max-width: calc(100vw - #{$modal-outer-spacing-mobile});

  @include media-breakpoint-up(md) {
    width: calc(100vw - #{$modal-outer-spacing-mobile});
    max-width: calc(((100vh - #{$modal-outer-spacing}) / 1.5) + #{$slickslide-outerwidth});
    max-height: calc(100vh - #{$modal-outer-spacing});
    height: calc(100vh - #{$modal-outer-spacing});
  }

  .show-spinner {
    width: 100%;
    height: 100%;
  }
}

.product-image-viewer-carousel {
  width: calc(100vw - #{$arrow-size} - #{$modal-outer-spacing-mobile} - #{$modal-inner-spacing-mobile});
  min-height: $slide-width;

  img {
    cursor: pointer;
  }

  @include media-breakpoint-down(md) {
    .slick-slide {
      div {
        width: $slide-width + $slide-item-spacing;
        padding-right: $slide-item-spacing;
      }
    }
  }


  @include media-breakpoint-up(md) {
    width: $slide-width;
    overflow: hidden;
    .slick-slide {
      margin-bottom: $slide-item-spacing;
    }
  }

  @include media-breakpoint-down(md) {
    &.invisible {
      position: absolute;
    }
  }

}


.viewer-zoom-in {
  @include getIcon("plus", $dark, 40);
}

.viewer-zoom-out {
  @include getIcon("minus", $dark, 40);
}

.viewer-zoom-in, .viewer-zoom-out {
  width: remCalc(40);
  height: remCalc(40);
  cursor: pointer;
}
