.image-promotion-component {

  max-width: remCalc(1300);

  .outer-col-left {
    @include media-breakpoint-up(md) {
      padding-right: remCalc(10);
    }
  }

  .outer-col-right {
    @include media-breakpoint-up(md) {
      padding-left: remCalc(10);
    }
  }

  .row-promotion {
    @include media-breakpoint-up(md) {
      height: 50%;
    }
  }

  .inner-col-left {
    padding-right: remCalc(10);
  }

  .inner-col-right {
    padding-left: remCalc(10);
  }

  .promotion-image {

    img {
      max-width: 100%;
      height: auto;
    }
  }

}