.header-icon {
  min-width: remCalc(40);

  .icon {
    .icon-overlay {
      position: absolute;
      width: 1rem;
      height: 1rem;
      top: remCalc(-6);
      right: remCalc(-7);

      .icon-logged-in {
        @include getIcon("checkmarkSmall", $white, 16);
      }
    }
  }

  &:hover .default-icon-login {
    @extend .default-icon-loginFill;
  }

  &:hover .default-icon-bag {
    @extend .default-icon-bagFill;
  }

  &:hover .default-icon-quickOrder {
    @extend .default-icon-quickOrderFill;
  }

  &:hover .default-icon-pin {
    @extend .default-icon-pinFill;
  }
}

.bg-icon-mobile {
  width: 2.5rem;
  height: 2.5rem;
}
