.category-slider {
  .category-slide {
    img {
      max-width: 100%;
      height: auto;

      &:focus {
        outline: 0;
      }
    }
    .category-slide-link {
      &:focus {
        outline: 0;
      }
    }
  }

  .slick-slide {
    margin: 0 remCalc(10);
  }

  .slick-list {
    margin: 0 remCalc(-10);
  }

  .slick-arrow {
    &.slick-disabled {
      visibility: hidden;
    }
  }
}
